/* import styled from '@emotion/styled'; */




.Form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 70px;
  padding-right: 70px;

  border: 2px solid #707070;
  border-radius: 8px;
  box-shadow: 0px 4px 4px #00000026;
  background-color: #ffd7b8;
}

.FormLabel {
  display: flex;
  flex-direction: column;
  align-items: center;

  font-size: 18px;
  font-weight: 500;
  color: #382600;

}

.FormBtn {
  border: none;
  font: inherit;
  cursor: pointer;
  outline: none;
  align-items: center;
  /* margin-left: auto; */
  border-radius: 4px;
  padding: 8px 48px;
  background-color: #382600;
  color: #ffffff;
  font-weight: 500;

  box-shadow: 0px 4px 4px #00000026;
}




.FormBtn:hover,
.FormBtn:focus {
  background-color: #795200;
}


.FormInput {
  font-weight: 600;
  color: #612c00;
}